// frontend/src/App.js

import React, { useState, useEffect } from 'react';
import axios from 'axios';
import MyCalendar from './Calendar';
import TaskForm from './TaskForm';
import TaskList from './TaskList';

const App = () => {
  const [events, setEvents] = useState([]); // state
  const [tasks, setTasks] = useState([]); // state

  useEffect(() => {
    fetchEvents(); // 
    fetchTasks(); // 
  }, []);


  const fetchEvents = async () => {
    try {
      const response = await axios.get('http://localhost:8000/api/tasks/'); // URL
      setEvents(response.data);
    } catch (error) {
      console.error('Error fetching events:', error);
    }
  };

  const fetchTasks = async () => {
    try {
      const response = await axios.get('/api/tasks/'); // DjangoREST API
      setTasks(response.data); // state
    } catch (error) {
      console.error('Error fetching tasks:', error);
    }
  };

  const handleEventSelect = (event) => {
    // 
    alert(`Event selected: ${event.title}`);
  };

  const handleDateSelect = (slotInfo) => {
    // 
    const newTask = {
      title: '',
      description: '',
      start_date: slotInfo.start,
      end_date: slotInfo.end,
    };
    setTasks([...tasks, newTask]); // 
  };

  const handleTaskAdd = async (task) => {
    try {
      await axios.post('/api/tasks/', task); // 
      fetchEvents(); // 
    } catch (error) {
      console.error('Error adding task:', error);
    }
  };


  return (
    <div>
      <h1>Calendar and Task Management App</h1>
      {/*  */}
      <MyCalendar events={events} onEventSelect={handleEventSelect} onDateSelect={handleDateSelect} />
      {/*  */}
      <TaskForm onTaskAdd={handleTaskAdd} />
      {/*  */}
      <TaskList tasks={tasks} />
    </div>
  );
};

export default App;
