import React, { useState } from 'react';

const TaskForm = ({ onTaskAdd }) => {
    const [task, setTask] = useState({
        title: '',
        description: '',
        start_date: '',
        end_date: '',
    });

    const handleInputChange = (event) => {
        const { name, value } = event.target;
        setTask((prevTask) => ({ ...prevTask, [name]: value }));
    };

    const handleSubmit = (event) => {
        event.preventDefault();
        onTaskAdd(task); // 
        setTask({ title: '', description: '', start_date: '', end_date: '' }); // 
    };

    return (
        <form onSubmit={handleSubmit}>
            <label>
                Title:
                <input type="text" name="title" value={task.title} onChange={handleInputChange} />
            </label>
            <br />
            <label>
                Description:
                <textarea name="description" value={task.description} onChange={handleInputChange} />
            </label>
            <br />
            <label>
                Start Date:
                <input type="datetime-local" name="start_date" value={task.start_date} onChange={handleInputChange} />
            </label>
            <br />
            <label>
                End Date:
                <input type="datetime-local" name="end_date" value={task.end_date} onChange={handleInputChange} />
            </label>
            <br />
            <button type="submit">Add Task</button>
        </form>
    );
};

export default TaskForm;
